/**
 * @method hasClass
 * @description domclass
 * @param { NodeObject } dom
 * @param { String } className className
 * @returns { Boolean } 
 */
export function hasClass(dom, className) {
  if (!dom || !className) { // domclassName
    return false;
  }
  if (dom.classList) {
    return dom.classList.contains(className);
  }
}

/**
 * @method addClass
 * @description domclassName
 * @param { NodeObject } dom classNamedom
 * @param { String } classNames className,
 */
export function addClass(dom, ...classNames) {
  if (!dom || !classNames) {
    return;
  }
  classNames.forEach(className => {
    dom.classList.add(className)
  })
}

/**
 * @method removeClass
 * @description domclassName
 * @param { NodeObject } dom dom
 * @param { String } classNames className,
 */
export function removeClass(dom, ...classNames) {
  if (!dom || !classNames) {
    return;
  }
  classNames.forEach(className => {
    dom.classList.remove(className)
  })
}

/**
 * @method setInlineStyle
 * @description 
 * @param { NodeObject } dom dom
 * @param { Object } styles 
 */
export function setInlineStyle(dom, styles = {}) {
  if (!dom) {
    return;
  }
  for (const key in styles) {
    // styles
    if (Object.hasOwnProperty.call(styles, key)) {
      dom.style[key] = styles[key]
    }
  }
}

/**
 * @method getStyle
 * @description dom
 * @param { Node } dom
 * @returns { Object }
 */
export function getStyle(dom) {
  if (dom.currentStyle) { // iedom
    return dom.currentStyle;
  }
  return getComputedStyle(dom) // ie
}

/**
 * @method hasClass
 * @description
 * @param { NodeObject }
 * @param { String } className
 * @returns { Boolean }
 */
export function getBrowserWidth() {
  if (document.body.clientWidth) {
    return {
      clientWidth: document.body.clientWidth,
      clientHeight: document.body.clientHeight
    };
  }
  return {
    clientWidth: document.documentElement.clientWidth,
    clientHeight: document.documentElement.clientHeight
  };
}

/**
 * @method on
 * @description 
 * @param { Node } target
 * @param { String } event
 * @param { Function } fn
 */
export function on(target, event, fn) {
  if (target && event && fn) {
    target.addEventListener(event, fn)
  }
}

/**
 * @method off
 * @description 
 * @param { Node } target
 * @param { String } event
 * @param { Function } fn
 */
export function off(target, event, fn) {
  if (target && event && fn) {
    target.removeEventListener(event, fn)
  }
}
